import { formatDate } from '@/util'
import Imgbig from '@/components/Imgbig'

export default {
  components: {
    Imgbig,
  },
  async asyncData ({ params }) {
    const article = await import(`~/content/posts/${params.slug}.md`);
    return {
      article
    };
  },
  methods: {
    formatDate (date) {
      return formatDate(date)
    }
  },
};
